/*样式重置 reset */
*{ margin:0; padding:0; }
body, h1, h2, h3, h4, h5, h6, p, dl, dd, ul, ol, input{
    margin:0;/*去掉自带外间距*/
    padding:0;/*去掉自带内填充*/
}
em, i{
    font-style:normal;/*斜体扶正*/
}
li{
    list-style:none;/*去掉默认列表符*/
}
h1, h2, h3, h4, h5, h6, b, strong{
    font-weight:normal;/*去掉标题自带加粗*/
}
a{
    outline:none;/*去掉描边*/
    text-decoration:none;/*去掉自带下划线*/
}
img{
    border:none;/*去掉ie浏览器图片外面的边框*/
    vertical-align:top;/*图片对齐方式*/
}
table{
    border-collapse:collapse;/*去掉表格自带间隙*/
    border-spacing:0;
}
input{
    outline:none;
    border:none;
    background-color:transparent;/*背景色透明*/
}
textarea{
    outline:none;
    resize:none;/*禁止调整文本域尺寸*/
    overflow:auto;/*在浏览器默认状态下没有滚动条*/
}
body{
    font-family:Arial,"\5FAE\8F6F\96C5\9ED1", Helvetica, sans-serif;
    font-size:12px;
}
/*public style*/
.clearfix{
    zoom:1;/*IE浏览器清浮动*/
}
.clearfix:after{
    content:'';/*添加一个空内容*/
    display:block;/*转块*/
    clear:both;/*清浮动*/
}
.fl{
    float:left;
}
.fr{
    float:right;
}
